import { Pipe, PipeTransform } from '@angular/core'

@Pipe({ name: 'roundNum' })

export class roudNum implements PipeTransform {
  transform(value: number, isUpper: boolean, toAdd: number): number {
    if (isUpper) {
      // ceil lam tron len
      return Math.ceil(value), value + toAdd;

    }
      // floor lam tron xuong
      return Math.floor(value);
    }

}
