import { Component, Output, EventEmitter } from "@angular/core";

@Component({
  selector: 'app-child',
  template: `
              <button (click)="Add()" >Add for Parent</button><br>
              <button (click)="Sub()" >Sub for Parent</button>
              <h3>{{ value }}<h3>
            `
})
export class ChildCompanent {

  // Parent tương tác tới Child
      @Output() ClickAdd = new EventEmitter();
      Add() {
        this.ClickAdd.emit(true);
      }
      Sub() {
        this.ClickAdd.emit(false)
      }

  //Child tương tác tới Parent
  value = 0;
}
