import { Component } from '@angular/core';

@Component({
  selector: 'app-login',
  template: `
  <form (ngSubmit)="OnSubmit(FromDemo)" #FromDemo="ngForm">
    <input placeholder="email>" name="Email" ngModel required  /><br><br>
    <p *ngIf="FromDemo.controls.Email?.errors?.required" >Nhap Email</p>
    <input type="password" placeholder="password" name="Pass"  ngModel /><br><br>
    <button [disabled]="FromDemo.invalid" >Submit</button>
  </form>

`
})

export class LoginComponent {

  OnSubmit(FromDemo) {
    console.log(FromDemo);
    throw new Error('From is valid');
  }
}
