import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-struct',
  templateUrl: './struct.component.html',
  styleUrls: ['./struct.component.sass']
})
export class StructComponent implements OnInit {
  arrSubject = ['Angular', 'NodeJS', 'Rubi'];
  Word = { fontSize: '20px', background: '#dcdcdc', width: '100px', margin:'10px'};
  En = "";
  Vn = "";
  ShowAdd = false;
  TrangThai = "Xem_TatCa";
  null = true;
  memoriz = "";

  arrWords = [
    { id: 1, en: 'action', vn: 'hanh dong', memorized: true },
    { id: 2, en: 'actor', vn: 'dien vien', memorized: false },
    { id: 3, en: 'activity', vn: 'hoat dong', memorized: true },
    { id: 4, en: 'active', vn: 'chu dong', memorized: true },
    { id: 5, en: 'bath', vn: 'tam', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phong ngu', memorized: true },
  ]
  Add() {
    this.arrWords.unshift({
      id: this.arrWords.length + 1,
      en: this.En,
      vn: this.Vn,
      memorized: false
    });
    this.En = "";
    this.Vn = "";
    this.ShowAdd = !this.ShowAdd;
  }
  ShowADD() {
    this.ShowAdd = !this.ShowAdd;
  }



  Delete(id: number) {
    const index = this.arrWords.findIndex(show => show.id === id);
    this.arrWords.splice(index, 1);
  }
  getLocTrangThai(memorized: boolean) {
    const TatCa = this.TrangThai === 'Xem_TatCa';
    const DaNho = this.TrangThai === 'Xem_DaNho' && memorized;
    const ChuaNho = this.TrangThai === 'Xem_ChuaNho' && !memorized;

    return TatCa || DaNho || ChuaNho;
  }



  constructor() { }


  ngOnInit() {
  }

}
