import { Component, ViewChild } from "@angular/core";
import { ChildCompanent } from "./child.component"
@Component({
  selector: 'app-parent',
  template: `<h3>Parent Thay doi gia tri Child</h3>
              <h3>{{ value }}</h3>
             <app-child (ClickAdd)="changeValue($event)" #Child ></app-child>
             <h3>Child thay doi gia tri Parent</h3>
              <button (click)="Child.value = Child.value + 1">Add for Child</button>
              <button (click)="onAddforChild()" >Sub for Child( cach 2)</button>
              `
})
export class ParentCompanent {

  // Parent tương tác với Child
  value = 0;
  Child = "";
  changeValue(Math: boolean) {
    if (Math) { this.value = this.value + 1 }
    else { this.value = this.value - 1 };
  }
  // Child tương tác với Parent
 // @ViewChild(ChildCompanent)
}
